* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  text-decoration: none;
  list-style: none;
  outline: none;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  flex-direction: column;
}

body img {
  width: 100px;
}

body .header {
  font-size: 70px;
  margin-top: 40px;
}

body a {
  font-size: 18px;
  color: grey;
  margin-top: 20px;
  border-bottom: 1px solid grey;
  transition: 0.1s ease;
}

body a:hover {
  color: #34C759;
  border-bottom: 1px solid #34C759;
}